.noPadding {
    margin-bottom: 12px;
    padding-top: 0;
}

.grid {
    padding: 16px 24px;
    display: flex;
    gap: 10px;
}

.editVideo {
    width: 50%;
    background-color: rgba(22, 24, 35, 0.06);
    border: 0.5px solid rgba(22, 24, 35, 0.12);
    box-shadow: white 0px 0px 0px 8px;
    border-radius: 8px;
    height: 84px;
    display: flex;
    -webkit-box-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    align-items: center;
}

.imageContainer {
    height: 100%;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
}

.imageNumber {
    width: 32px;
    height: 100%;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    margin-right: 8px;
    span {
        margin-block: 0px;
        font-size: 16px;
        line-height: 1.5;
        font-weight: 700;
        color: rgba(22, 24, 35, 0.34);
    }
}

.image {
    width: 35px;
    height: 52px;
    background-color: black;
    position: relative;
    border-radius: 2px;
}

.card {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 35px;
    height: 52px;
    z-index: 1;
    background: rgba(22, 24, 35, 0.06);
    .imageCard {
        width: 100%;
        height: 100%;
        border-radius: 2px;
        object-fit: contain;
        transition: opacity 0.5s ease 0s;
    }
}

.videoImageDescContainer {
    margin-left: 8px;
    .videoImageDescName {
        width: 230px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: inline-block;
        white-space: nowrap;
        font-size: 14px;
        line-height: 1.5;
        font-weight: 400;
        color: rgba(22, 24, 35, 1);
    }
    .videoImageDescTime {
        span {
            font-size: 12px;
            line-height: 1.5;
            font-weight: 400;
            color: rgba(22, 24, 35, 0.5);
        }
        span:last-child {
            margin-left: 12px;
        }
    }
}

.editBtnContainer {
    margin-right: 24px;
}

.split {
    width: 50%;
    border: 0.5px dashed rgba(22, 24, 35, 0.34);
    height: 84px;
    border-radius: 8px;
    padding: 15px 24px;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    justify-content: space-between;
}

.splitContainer {
    span {
        font-size: 12px;
        line-height: 1.5;
        font-weight: 400;
        color: rgba(22, 24, 35, 0.34);
    }
    .handleSplit {
        display: flex;
        -webkit-box-align: center;
        align-items: center;
        margin-top: 4px;
        user-select: none;
        span:first-child,
        span:last-child {
            cursor: not-allowed;
            background-color: rgba(0, 0, 0, 0);
            border-top-left-radius: 2px;
            border-bottom-left-radius: 2px;
            width: 32px;
            height: 32px;
            border: 0.5px solid rgba(22, 24, 35, 0.12);
            display: flex;
            -webkit-box-align: center;
            align-items: center;
            -webkit-box-pack: center;
            justify-content: center;
        }
        span:nth-child(2) {
            border-bottom: 0.5px solid rgba(22, 24, 35, 0.12);
            border-top: 0.5px solid rgba(22, 24, 35, 0.12);
            width: 48px;
            height: 32px;
            display: flex;
            -webkit-box-align: center;
            align-items: center;
            -webkit-box-pack: center;
            justify-content: center;
            input {
                font-weight: 600;
                width: 48px;
                height: 24px;
                outline: none;
                cursor: text;
                color: rgba(22, 24, 35, 1);
                border-style: solid;
                border-width: 0.5px;
                border-color: transparent;
                background: transparent;
                padding: 0px;
                text-align: center;
            }
        }
    }
}

.splitBtnContainer {
    .splitBtn {
        margin-right: 0;
        cursor: not-allowed;
        color: rgba(22, 24, 35, 0.34);
        background-color: rgba(235, 235, 235, 1);
        border-color: rgba(235, 235, 235, 1);
    }
}
