.description {
    color: rgb(22, 24, 35);
    font-weight: 600;
    font-size: 15px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
}

.change-link {
    color: rgba(22, 24, 35, 0.75);
    font-weight: 500;
    font-size: 12px;
    display: inline-block;
    &:hover {
        text-decoration: underline;
    }
}

.phone-container {
    background: rgba(22, 24, 35, 0.06);
    border: 1px solid rgba(22, 24, 35, 0.12);
    border-radius: 4px;
    margin-bottom: 9px;
    display: flex;
    position: relative;
    &::after {
        content: '';
        position: absolute;
        top: 7px;
        left: 98px;
        width: 1px;
        height: 30px;
        background: rgba(22, 24, 35, 0.12);
    }
}

.code-container {
    position: relative;
    cursor: pointer;
}

.zone-code {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px;
    height: 44px;
}

.vn-code {
    font-size: 16px;
    line-height: 1;
    color: rgb(22, 24, 35);
    margin-inline-end: 12px;
}

.search-code-btn {
    cursor: pointer;
}

.turn-around {
    transform: rotate(180deg);
    transition-duration: 0.3s;
}

.turn-back {
    transform: rotate(0deg);
    transition-duration: 0.3s;
}

.phone-number {
    background: transparent;
    border-radius: 2px;
    padding-inline-start: 12px;
    color: rgb(22, 24, 35, 0.75);
    line-height: 100%;
    outline: none;
    font-size: 15px;
    height: 44px;
    caret-color: rgb(254, 44, 85);
    padding-inline-end: unset;
}

.send-code {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
}

.input-container {
    position: relative;
    width: 100%;
}

.input-code {
    border: 2px 0 0 2px;
    background-color: rgba(22, 24, 35, 0.06);
    border: 1px solid rgba(22, 24, 35, 0.12);
    border-inline-end-width: 0px;
    padding-inline-start: 12px;
    color: rgb(22, 24, 35, 0.75);
    line-height: 100%;
    outline: none;
    font-size: 1.5rem;
    height: 44px;
    caret-color: rgb(254, 44, 85);
    width: 100%;
    padding-inline-end: unset;
}

.code-btn {
    color: rgb(22, 24, 35);
    // font-family: ProximaNova, Arial, Tahoma, PingFangSC, sans-serif;
    font-weight: 600;
    background-color: rgba(22, 24, 35, 0.06);
    border: 1px solid rgba(22, 24, 35, 0.12);
    border-radius: 0px 4px 4px 0px;
    cursor: pointer;
    font-size: 16px;
    padding: 0px 16px;
    flex-shrink: 0;
}

.disabled-code-btn {
    color: rgba(22, 24, 35, 0.34);
    cursor: not-allowed;
}

.email-container,
.password-container {
    margin-bottom: 9px;
}

.password-container {
    position: relative;
}

.hide-icon,
.show-icon {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
}

.email-address,
.password {
    border-radius: 2px;
    background-color: rgba(22, 24, 35, 0.06);
    border: 1px solid rgba(22, 24, 35, 0.12);
    padding-inline-start: 12px;
    color: rgb(22, 24, 35);
    line-height: 100%;
    outline: none;
    font-size: 1.5rem;
    height: 44px;
    caret-color: rgb(254, 44, 85);
    width: 100%;
}

//divide
.label {
    display: flex;
}
.divide {
    display: block;
    width: 1px;
    height: 12px;
    margin: 0px 16px;
    background: rgba(22, 24, 35, 0.12);
}

.login-btn {
    border-radius: 4px;
    border: none;
    color: rgb(255, 255, 255);
    background-color: rgb(254, 44, 85);
    min-width: 120px;
    min-height: 46px;
    font-size: 16px;
    line-height: 22px;
    font-weight: 700;
    font-family: var(--font-family);
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    padding: 6px 8px;
    user-select: none;
    cursor: pointer;
    box-sizing: border-box;
    width: 100%;
    margin-top: 21px;
}
