.copyright {
    margin-top: 24px;
}

.switch {
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 6px;
}

.copyrightDesc {
    span {
        font-size: 12px;
        line-height: 1.5;
        font-weight: 400;
        color: rgba(22, 24, 35, 0.75);
        a {
            line-height: 1.5;
            font-weight: 600;
            color: rgba(22, 24, 35, 1);
        }
    }
}

.btn-row {
    display: flex;
    margin-top: 24px;
}

.discardBn,
.postBtn {
    width: 164px;
    height: 48px;
}

.user-avatar {
    width: 32px;
    height: 32px;
    object-fit: cover;
    border-radius: 50%;
    margin-left: 14px;
    cursor: pointer;
}

.confirmTitle {
    padding: 32px 32px 24px;
    div {
        font-weight: 600;
        font-size: 26px;
        line-height: 30px;
        color: rgb(22, 24, 35);
        word-break: break-word;
        text-align: center;
    }
}

.upload,
.view {
    padding: 8px 20px;
}

.view {
    margin-bottom: 24px;
}

.uploadBtn,
.viewProfileBtn {
    width: 100%;
}

.uploadBtn {
    padding: 13px 16px;
}

.viewProfileBtn {
    padding: 9px 16px;
}

.viewProfileBtn {
    margin-right: 0;
}
